body {
  margin: 0;
  background-color: #333333;
  color: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.header{
    padding: 0 20px;
    width: calc (100vw - 20px);
    height: 80px;
    border-bottom: 1px solid #eeeeee;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
}

.header-execute{
    width: 160px;
    height: 40px;
    border: 0;
    border-radius: 8px;
    background-color: #888888;
    color: #ffffff;
    transition: .05s;
    font-size: 1em;
}
.header-execute:hover{
    background-color: #aaaaaa;
}

.active{
    background-color: #6696e0;
}
.active:hover{
    background-color: #96c6f0;
}
.body-content{
    width: 100vw;
    height: calc(100vh - 81px);
    display: flex;
}

.block-container{
    display:flex;
    width: 200px;
    height: 100%;
    margin-left: 30px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: .1s;
}

.slot-container{
    display: flex;
    width: calc(100vw - 200px);
    height: 100%;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 850px){
    .slot-container{
        flex-direction: column;
    }
}
.block{
    width: 160px;
    max-width: 90%;
    padding: 8px;
    overflow-wrap: break-word;
    text-overflow: ellipsis;
    height: 60px;
    border: 0;
    margin : 10px;
    color: #ffffff;
    border-radius: 6px;
    box-shadow: 0 2px 8px #000000;
    text-align: center;
}

.data{
    background-color: #555555;
}

.func{
    background-color: #8d8d8d;
}
.slot{
    display:flex;
    position:relative;
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 200px;
    border: 0;
    background-color: #777777;
    box-sizing: border-box;
    border-radius: 8px;
}

.filled{
    
    background-color: #cccccc;
    color: #111111;
}

.declined{
    border: 2px solid red;
}

.acceptable{
    border: 3px dotted seagreen;
}
